    <div v-else-if="lang===`en`">
      <h4 class="fw-bold">The "Russian waltz" in the Polish Borderlands - Piotr Korczyński</h4>
      <p class="fw-bold">
        "Backstab!" - this is how the British Times called its first column a day after the Soviet aggression against Poland. The title accurately reflected the actions of the Red Army, which fulfilled allied obligations towards the Wehrmacht, but at the same time took advantage of the inaction of Poland's western allies, who gave it to two totalitarian neighbors.
      </p>
      <p>
        Saturday, September 16, 1939 was a dark day for the SS troops fighting in Poland. Hitler's army was badly depleted by Polish soldiers. First, in the forests of Jaworów, troops of Gen. Kazimierz Sosnkowski, going to the rescue of Lviv, defeated the Motorized Regiment of the SS "Germania", and then one battery of the 17th Light Artillery Regiment near Sochaczew stopped the attack of the entire 4th Armored Division, including the elite "Leibstandarte SS Adolf Hitler" Regiment. Several Polish guns damaged and destroyed 22 German tanks and armored cars! The news made the Führer's headquarters furious. Hitler had the right to be upset - despite the assurances of his commanders that they would capture Warsaw in a few days, the Polish capital continued to defend itself, and the Polish Army was still able to inflict painful blows. To make matters worse, Stalin delayed the fulfillment of the agreement contained in the Ribbentrop-Molotov pact and did not attack Poland from the east, despite the increasingly insistent exhortations from Berlin.
      </p>

      <h4 class="fw-bold">Kremlin chimes</h4>
      <p>
        Stalin delayed the attack on Poland because, despite his alliance with Hitler, he still looked to the West. He knew that the Red Army, after the purge of its command, which he himself had ordered, was not yet ready to fight the Allied forces. However, on the same day that Polish soldiers defeated the SS regiments, the French and English staff at the Abbeville conference decided that they would not move to help Poles by force, which Soviet agents eagerly reported to the authorities in the Kremlin. After receiving this news, Stalin had no resistance and ordered the summons to be sent to the troops which he had concentrated on his western border.
      </p>
      <p>
        In the morning of September 17, the Soviets summoned the Polish ambassador to Moscow, Wacław Grzybowski, and presented him with a note stating that “the Polish state and its government ceased to exist. Therefore, the treaties concluded between the USSR and Poland are no longer valid. [...] The Soviet Government cannot remain indifferent to the fact that the related people of Ukrainian and Belarusian origin living in Poland are defenseless and have been left to their own fate. In view of the above circumstances, the Soviet government ordered the Supreme Command of the Red Army to order the troops to cross the border and take care of the lives and property of the people of Western Ukraine and Western Belarus ”. The statement that the Polish state did not exist was to secure the Soviet Union in the event of a reaction from Western powers and create the appearance of neutrality in the conflict between Germany, France and Great Britain, which was fully successful.
      </p>
      <p>
        Even more, because despite the fact that Ambassador Grzybowski did not accept the false note, the Polish authorities, under pressure from their Western allies, refrained from issuing a statement that they were at war with the Soviet Union. The West still hoped to win the Soviets over to its side. On the other hand, the Polish civil and military authorities were surprised by the Soviet attack, although the intelligence had long informed about the concentration of Soviet troops on the border. The Commander-in-Chief, Marshal Edward Rydz-Śmigły, only issued a directive in the evening of September 17 that Warsaw and Modlin would resist until their ammunition was exhausted, and all units would withdraw to Hungary or Romania. He allowed to fight the Soviets only if they attacked Polish troops or tried to disarm them. The latter statement only introduced chaos, as Polish commanders interpreted it differently and were not sure how to behave. Especially since the Soviets often proclaimed for sabotage purposes that they were coming to Poland's aid.
      </p>
      <p>
        On the night of September 17-18, the president and the government of the Republic of Poland went to Romania. The same was done by Marshal Rydz-Śmigły along with his staff, which caused indignation in a large part of Polish society and resulted in accusations of abandoning the still fighting army.
      </p>

      <h4 class="fw-bold">Buoys with the Soviet colossus</h4>

      <p>
        The Soviet troops attacking Poland were formed into two fronts. In the first hours of the attack, the Belarusian Front, commanded by Mikhail Kovalov, consisted of 15 infantry and cavalry divisions and seven armored brigades (over 2,000 tanks and armored cars) - a total of over 200,000. soldiers, and this number increased in the following days. On the other hand, the Ukrainian Front, commander Semyon Timoshenko, had 16 infantry and cavalry divisions and eight armored brigades (nearly 3,000 tanks and armored cars), i.e. over 265 thousand. soldiers, supported by an air fleet of about a thousand aircraft. Moreover, regular units of the Red Army supported sabotage groups created and armed by the Soviet military intelligence and NKVD from Belarusian, Ukrainian and Jewish citizens of the Republic of Poland.
      </p>
      <p>
        Poles could counter this onslaught in the Eastern Borderlands by disproportionately weaker forces. The Polish command left about 400,000 to protect these lands. soldiers. The border was directly guarded by nearly 12 thousand. soldiers of the Border Protection Corps, organized in 25 battalions and seven cavalry squadrons. Most of these units, despite the overwhelming advantage of the enemy, took up the fight against him. Fierce battles continued in the Vilnius, Nowogródek and Podolia regions. For three days the Soviet onslaught was stopped by the KOP "Sarny" fortress battalion in its fortified position near Słucz. On September 20 and 21, street fights took place in Grodno, during which Soviet armored units suffered significant losses. On September 22, at Kodziowce, the 101st Uhlans successfully stopped the Soviet attack, destroying many of their tanks.
      </p>
      <p>
        The KOP group of General Wilhelm Orlik-Rückeman, retreating from the border in Polesie, had the most clashes with the Red Army. Its greatest success was the defeat of the staff and some units of the 52nd Rifle Division at Szack on September 28. He fought equally effectively in the area of Borowicz, Nawóz and Hruziatyn, the 3rd KOP Infantry Regiment, Col. Zdzisław Zajączkowski. Also the units of the Independent Operational Group "Polesie", General Franciszek Kleeberg, clashed with the Red Army more than once, incl. near Jabłonia and Milanów. However, Polish troops did not have a chance to stop the Soviet colossus. Although the Polish campaign clearly showed that the Red Army is led by mostly incompetent commanders, and the soldiers are poorly trained and equipped, they literally flooded the Polish Borderlands with their mass - the infamous "Russian waltz". The Soviets also very quickly stopped pretending that they did not cooperate with the Germans, which was an eloquent symbol of, among others, a joint military parade in Brest-on-the-Bug. On September 23, "Prawda" began to publish maps with a line separating the German and Soviet gains, proving clearly that the Third Reich and the Soviet Union were carrying out the fourth partition of Poland.
      </p>

