    <div v-else-if="lang===`it`">
      <h4 class="fw-bold">Il "valzer russo" nei confini polacchi - Piotr Korczyński</h4>
      <p class="fw-bold">
        "Pugnalata alle spalle!" - è così che il British Times ha chiamato la sua prima colonna il giorno dopo l'aggressione sovietica contro la Polonia. Il titolo rifletteva accuratamente le azioni dell'Armata Rossa, che adempiva agli obblighi degli alleati nei confronti della Wehrmacht, ma allo stesso tempo approfittava dell'inazione degli alleati occidentali della Polonia, che lo cedevano a due vicini totalitari.


      </p>
      <p>
        Sabato 16 settembre 1939 fu un giorno buio per le truppe delle SS che combattevano in Polonia. L'esercito di Hitler è stato gravemente impoverito dai soldati polacchi. Per prima cosa, nelle foreste di Jaworów, le truppe del generale Kazimierz Sosnkowski, andando in soccorso di Leopoli, sconfissero il reggimento motorizzato delle SS "Germania", quindi una batteria del 17 ° reggimento di artiglieria leggera vicino a Sochaczew fermò l'attacco del l'intera 4a divisione corazzata, compreso il reggimento d'élite "Leibstandarte SS Adolf Hitler". Diversi cannoni polacchi hanno danneggiato e distrutto 22 carri armati tedeschi e auto blindate! La notizia fece infuriare il quartier generale del Führer. Hitler aveva il diritto di essere sconvolto: nonostante le assicurazioni dei suoi comandanti che avrebbero catturato Varsavia in pochi giorni, la capitale polacca continuò a difendersi e l'esercito polacco era ancora in grado di infliggere colpi dolorosi. A peggiorare le cose, Stalin ha ritardato l'adempimento dell'accordo contenuto nel patto Ribbentrop-Molotov e non ha attaccato la Polonia da est, nonostante le esortazioni sempre più insistenti da Berlino.
      </p>

      <h4 class="fw-bold">Suona il Cremlino</h4>
      <p>
        Stalin ritardò l'attacco alla Polonia perché, nonostante la sua alleanza con Hitler, guardava ancora all'Occidente. Sapeva che l'Armata Rossa, dopo l'epurazione del suo comando, che lui stesso aveva ordinato, non era ancora pronta a combattere le forze alleate. Tuttavia, lo stesso giorno in cui i soldati polacchi sconfissero i reggimenti delle SS, il personale francese e inglese alla conferenza di Abbeville decise che non si sarebbero mossi per aiutare i polacchi con la forza, cosa che gli agenti sovietici riferirono con entusiasmo alle autorità del Cremlino. Dopo aver ricevuto questa notizia, Stalin non ebbe resistenza e ordinò che la convocazione fosse inviata alle truppe che aveva concentrato sul suo confine occidentale.
      </p>
      <p>
        La mattina del 17 settembre, i sovietici convocarono l'ambasciatore polacco a Mosca, Wacław Grzybowski, e gli presentarono una nota in cui affermava che “lo Stato polacco e il suo governo hanno cessato di esistere. Pertanto, i trattati conclusi tra l'URSS e la Polonia non sono più validi. [...] Il governo sovietico non può rimanere indifferente al fatto che le persone imparentate di origine ucraina e bielorussa che vivono in Polonia sono indifese e sono state lasciate al loro destino. In considerazione delle circostanze di cui sopra, il governo sovietico ha ordinato al Comando supremo dell'Armata Rossa di ordinare alle truppe di attraversare il confine e di prendersi cura delle vite e delle proprietà della popolazione dell'Ucraina occidentale e della Bielorussia occidentale ”. L'affermazione che lo Stato polacco non esisteva era per proteggere l'Unione Sovietica in caso di reazione delle potenze occidentali e creare un'apparenza di neutralità nel conflitto tra Germania, Francia e Gran Bretagna, che ebbe pieno successo.
      </p>
      <p>
        Ancor di più, perché nonostante il fatto che l'ambasciatore Grzybowski non abbia accettato la falsa nota, le autorità polacche, sotto la pressione dei loro alleati occidentali, si sono astenute dal rilasciare una dichiarazione di guerra con l'Unione Sovietica. L'Occidente sperava ancora di conquistare i sovietici dalla sua parte. D'altra parte, le autorità civili e militari polacche furono sorprese dall'attacco sovietico, sebbene l'intelligence fosse da tempo informata della concentrazione di truppe sovietiche al confine. Il comandante in capo, il maresciallo Edward Rydz-Śmigły, ha emesso solo una direttiva la sera del 17 settembre che Varsavia e Modlin avrebbero resistito fino a quando le loro munizioni non fossero state esaurite e tutte le unità si sarebbero ritirate in Ungheria o in Romania. Ha permesso di combattere i sovietici solo se hanno attaccato Le truppe polacche o hanno cercato di disarmarle. Quest'ultima affermazione ha solo introdotto il caos, poiché i comandanti polacchi lo interpretavano in modo diverso e non erano sicuri di come comportarsi. Tanto più che i sovietici proclamavano spesso a scopo di sabotaggio che stavano andando in aiuto della Polonia.
      </p>
      <p>
       
Nella notte tra il 17 e il 18 settembre, il presidente e il governo della Repubblica di Polonia si sono recati in Romania. Lo stesso è stato fatto dal maresciallo Rydz-Śmigły insieme al suo staff, che ha causato indignazione in gran parte della società polacca e ha portato ad accuse di abbandono dell'esercito ancora in combattimento.


      </p>

      <h4 class="fw-bold">Boe con il colosso sovietico</h4>

      <p>
        Le truppe sovietiche che attaccavano la Polonia erano formate su due fronti. Nelle prime ore dell'attacco, il fronte bielorusso, comandato da Mikhail Kovalov, era composto da 15 divisioni di fanteria e cavalleria e sette brigate corazzate (oltre 2.000 carri armati e auto blindate), per un totale di oltre 200.000. soldati, e questo numero è aumentato nei giorni successivi. D'altra parte, il Fronte ucraino, comandante Semyon Timoshenko, aveva 16 divisioni di fanteria e cavalleria e otto brigate corazzate (quasi 3.000 carri armati e auto blindate), cioè oltre 265mila. soldati, supportati da una flotta aerea di circa mille aerei. Inoltre, unità regolari dell'Armata Rossa hanno sostenuto gruppi di sabotaggio creati e armati dall'intelligence militare sovietica e dall'NKVD di cittadini bielorussi, ucraini ed ebrei della Repubblica di Polonia.
      </p>
      <p>
        I polacchi potrebbero contrastare questo assalto nelle terre di confine orientali da forze sproporzionatamente più deboli. Il comando polacco ha lasciato circa 400.000 persone per proteggere queste terre. soldati. Il confine era sorvegliato direttamente da quasi 12mila. soldati del Border Protection Corps, organizzati in 25 battaglioni e sette squadroni di cavalleria. La maggior parte di queste unità, nonostante il vantaggio schiacciante del nemico, ha intrapreso la lotta contro di lui. Sono continuate feroci battaglie nelle regioni di Vilnius, Nowogródek e Podolia. Per tre giorni l'assalto sovietico fu fermato dal battaglione della fortezza KOP "Sarny" nella sua posizione fortificata vicino a Słucz. Il 20 e il 21 settembre a Grodno si sono svolti combattimenti di strada, durante i quali le unità corazzate sovietiche hanno subito perdite significative. Il 22 settembre, a Kodziowce, il 101esimo Uhlans fermò con successo l'attacco sovietico, distruggendo molti dei loro carri armati.
      </p>
      <p>
        Il gruppo KOP del generale Wilhelm Orlik-Rückeman, in ritirata dal confine in Polesie, ha avuto il maggior numero di scontri con l'Armata Rossa. Il suo più grande successo fu la sconfitta del personale e di alcune unità della 52a Divisione Fucilieri a Szack il 28 settembre. Combatté altrettanto efficacemente nell'area di Borowicz, Nawóz e Hruziatyn, il 3 ° Reggimento di Fanteria KOP, Col. Zdzisław Zajączkowski. Anche le unità del gruppo operativo indipendente "Polesie", il generale Franciszek Kleeberg, si sono scontrate con l'Armata Rossa più di una volta, incl. vicino a Jablonia e Milanów. Tuttavia, le truppe polacche non hanno avuto la possibilità di fermare il colosso sovietico. Sebbene la campagna polacca abbia chiaramente dimostrato che l'Armata Rossa è guidata da comandanti per lo più incompetenti, e che i soldati sono scarsamente addestrati ed equipaggiati, hanno letteralmente inondato i confini polacchi con la loro massa - il famigerato "valzer russo". I sovietici smisero anche molto rapidamente di fingere di non cooperare con i tedeschi, che era un simbolo eloquente, tra gli altri, di una parata militare congiunta a Brest-on-the-Bug. Il 23 settembre "Prawda" iniziò a pubblicare mappe con una linea che separava le conquiste tedesche e sovietiche, dimostrando chiaramente che il Terzo Reich e l'Unione Sovietica stavano effettuando la quarta spartizione della Polonia.


      </p>

